* {
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(234, 237, 237);
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 2rem;
}
.app__heading {
  display: flex;
  padding: 10px auto;
  top: 0;
  position: sticky;
}
.app__container {
  display: flex;
  justify-content: space-evenly;
  padding-top: 20px;
  width: 100%;
  /* margin-left: 18rem;
  padding-left: 20rem; */
}

.app__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: white;
  border-radius: 10px solid white;
  flex: 0.4;
  padding-bottom: 10px;
}
.app__right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-bottom: 10px;
  background-color: white;
  flex: 0.4;
}
.table {
  /* margin-top: 40px; */
  overflow: scroll;
  color: #6a5d5d;
  background-color: white;
  height: 300px;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
  border: none;
  text-transform: capitalize;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}
@media (max-width: 760px) {
  .app__container {
    flex-direction: column;
    width: 80%;
  }
  .app__heading {
    background-color: white;
    padding-left: 11rem;
    width: 51%;
  }
  .app__right {
    padding-top: 1rem;
  }
}
